<template>
<div>
  <div v-if="loading" class="loading">
    <img src="../../assets/loading.gif" alt="loading">
    <p>...</p>
  </div>
  <PromptBox v-show="showPrompt" :promptMessage="promptMessage" />
  <main class="main" :class="{ blur: whetherBlur}">
    <div class="mask"></div>
    <img
      @click="switchBackgroundMusic"
      class="music"
      :src="musicIconSrc"
      alt="music"
    />
    <router-link to="/invitation">
      <button class="invite-code head-btn"></button>
    </router-link>
    <router-link to="/rank">
      <button class="rank-list head-btn">&nbsp;<Icon name="angle-right" /></button>
    </router-link>
    <!---->
    <section class="info-panel">
      <img class="avatar" v-lazy="headImgUrl" alt="">
      <div class="text-info">
        <div>
          <p class="title"></p>
          <router-link to="/share">
            <p class="content">
              {{ gameNumber[0] }}/{{ gameNumber[1] }}&nbsp;
              <Icon class="icon" name="angle-right" />
            </p>
          </router-link>
        </div>
        <div></div>
        <div>
          <p class="title"></p>
          <router-link to="prize">
            <p class="content">
              &yen;&nbsp;{{ prize }}&nbsp;
              <Icon class="icon" name="angle-right" />
            </p>
          </router-link>
        </div>
      </div>
      <!---->
      <Button
        class="practice-btn"
        @click.native="play('practice')"
      >
        <span class="text"> ()</span>
      </Button>
      <Button
        class="normal-btn"
        @click.native="play('normal')"
      >
        <img class="begin" src="../../assets/icon/begin.svg" alt="begin">
        <span class="text"></span>
      </Button>
      <Button
        class="activity-btn"
        @click.native="play('activity')"
        :propsStyle="{ backgroundColor: '#198cf9' }"
      >
        <img class="activity-img" src="../../assets/activity.png" alt="begin">
        <span class="activity-intro">
          <span></span>
          <span></span>
        </span>
      </Button>
      <!---->
      <div class="trailer">
        <div class="title"></div>
        <div class="content">
          <p>{{ trailerTime }}</p>
          <div></div>
          <p>&yen;&nbsp;{{ trailerPrize }}&nbsp;</p>
        </div>
      </div>
      <!---->
      <div class="divider"></div>
      <!---->
      <ScrollMessage class="message" :messages="messages" />
    </section>
    <div class="help">
      <Icon name="question-circle" class="help-icon" />
      <router-link to="/rules"></router-link>
    </div>
  </main>
  <router-view @showPromptBox="showPromptBox" />
</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import Icon from 'vue-awesome/components/Icon';
import ScrollMessage from '../../components/ScrollMessage';
import PromptBox from '../../components/PromptBox';
import Button from '../../components/Button';
import { GET_CACHE, SET_USER_INFO, SWITCH_MUSIC, PLAY_GAME } from '../../store/mutation-types';
import { getUserInfo, playGame } from '../../api';
import musicIcon from '../../assets/icon/background-music.svg';
import muteMusicIcon from '../../assets/icon/background-music-mute.svg';

export default {
  name: 'HomePage',
  data() {
    return {
      loading: false,
      showPrompt: false,
      whetherBlur: false,
      promptMessage: '',
      trailerTime: '',
      trailerPrize: 0,
      messages: []
    };
  },
  methods: {
    play(type) {
      switch (type) {
        case 'normal':
          if (this.gameNumber[0] <= 0) {
            this.showPromptBox('');
            return;
          }
          break;
        case 'practice':
          if (this.practiceNumber <= 0) {
            this.showPromptBox('');
            return;
          }
          break;
        case 'activity':
          break;
        default:
      }
      playGame({ openid: this.openid, type })
        .then((res) => {
          if (res.data.state) {
            this.commitPlayGame(type);
            this.$router.push('/countdown');
          } else {
            this.showPromptBox(res.data.msg);
          }
        })
        .catch(() => {
          this.showPromptBox('');
        });
    },
    showPromptBox(msg) {
      if (msg === '') return;
      this.promptMessage = msg;
      this.showPrompt = true;
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        this.showPrompt = false;
      }, 2500);
    },
    judgeBlur() {
      this.whetherBlur = ['/invitation', '/share', '/prize'].indexOf(this.$route.path) > -1;
    },
    switchBackgroundMusic() {
      this.switchMusic();
      // 
      if (this.mute) {
        this.showPromptBox('');
      } else {
        this.showPromptBox('');
      }
    },
    loadUserInfo() {
      getUserInfo().then(({ data }) => {
        this.setUserInfo(data);
        this.messages = data.messages;
        this.trailerPrize = data.trailer.prize || 0;
        const trailerTime = new Date(data.trailer.time);
        const restDay = trailerTime.getDate() - new Date().getDate();
        switch (restDay) {
          case 0:
            this.trailerTime = ` ${trailerTime.getHours()}:${trailerTime.getMinutes()}`;
            break;
          case 1:
            this.trailerTime = ` ${trailerTime.getHours()}:${trailerTime.getMinutes()}`;
            break;
          case 2:
            this.trailerTime = ` ${trailerTime.getHours()}:${trailerTime.getMinutes()}`;
            break;
          default:
            this.trailerTime = `${trailerTime.getMonth() + 1 || 0}${trailerTime.getDate() || 0} ${trailerTime.getHours() || 0}:${trailerTime.getMinutes() || 0}`;
        }
      });
    },
    ...mapMutations({
      getCache: GET_CACHE,
      setUserInfo: SET_USER_INFO,
      switchMusic: SWITCH_MUSIC,
      commitPlayGame: PLAY_GAME
    })
  },
  computed: {
    musicIconSrc() {
      return this.mute ? muteMusicIcon : musicIcon;
    },
    ...mapState([
      'openid',
      'headImgUrl',
      'gameNumber',
      'practiceNumber',
      'prize',
      'mute'
    ])
  },
  watch: {
    $route() {
      this.judgeBlur();
    }
  },
  components: {
    Icon,
    ScrollMessage,
    PromptBox,
    Button
  },
  created() {
    // 
    this.getCache();
    // 
    this.judgeBlur();
    this.loadUserInfo();
    if (this.$route.params.message) {
      this.$nextTick(() => {
        this.showPromptBox(this.$route.params.message);
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" src="./style.scss"></style>



