<template>
  <transition name="slide-fade">
    <div class="list-container">
      <header class="header">
        <router-link to="/">
          <img class="close-icon" src="../../assets/icon/close-white.svg" alt="close">
        </router-link>
        <h1 class="heading"></h1>
      </header>
      <div class="content">
        <nav>
          <button
            :class="{ active: rankingType === 'week' }"
            @click="rankingType = 'week'"
          >
            </button>
          <button
            :class="{ active: rankingType === 'total' }"
            @click="rankingType = 'total'"
          >
            
          </button>
        </nav>
        <div class="top-list">
          <div
            class="top-list-box"
            v-for="(item, index) in rankingList"
            v-if="index <= 2"
            :key="item.userId"
          >
            <img class="avatar" v-lazy="item.headImgUrl" alt="avatar">
            <p class="user-name">{{ item.nickname }}</p>
            <p class="score">{{ item.score }}</p>
          </div>
        </div>
        <div class="list">
          <div
            class="list-box"
            v-for="(item, index) in rankingList"
            v-if="index > 2"
            :key="item.userId"
            :class="{ active: index +1 === rank }"
          >
            <span class="rank">{{ index + 1 }}</span>
            <img class="avatar" v-lazy="item.headImgUrl" alt="avatar">
            <div class="user-name">{{ item.nickname }}</div>
            <div class="score">{{ item.score }}</div>
          </div>
        </div>
        <div class="self list">
          <div class="list-box">
            <span class="rank">{{ rank }}</span>
            <img class="avatar" :src="headImgUrl" alt="avatar">
            <div class="user-name">{{ nickname }}</div>
            <div class="score">{{ score }}</div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapState } from 'vuex';
import { getRankingList } from '../../api';

export default {
  name: 'RankingList',
  data() {
    return {
      rankingType: 'total', // totalweek
      rankingList: []
    };
  },
  computed: mapState([
    'nickname',
    'headImgUrl',
    'score',
    'rank'
  ]),
  watch: {
    rankingType(newType) {
      getRankingList({ type: newType })
        .then(({ data }) => {
          this.rankingList = data.rankingList;
        });
    }
  },
  created() {
    getRankingList({ type: this.rankingType })
      .then(({ data }) => {
        this.rankingList = data.rankingList;
      });
  }
};
</script>

<style lang="scss" scoped src="./style.scss"></style>



