<template>
  <transition name="slide-fade">
    <div class="code-container" @click.self="goHome">
      <div class="modal">
        <router-link to="/">
          <img class="close-btn" src="../../assets/icon/close.svg" alt="close">
        </router-link>
        <h1></h1>
        <div class="input">
          <input
            v-model="filledCode"
            type="number"
            pattern="[0-9]"
            maxlength="6"
            min="0"
            max="999999"
            placeholder="6"
            autofocus
          >
          <button
            :class="{ active: filledCode.length === 6}"
            @click="submitCode"
          >
            
          </button>
        </div>
        <h2></h2>
        <p>{{ invitationCode }}</p>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapState } from 'vuex';
import { invite } from '../../api';

export default {
  name: 'InvitationCode',
  computed: mapState([
    'invitationCode'
  ]),
  data() {
    return {
      filledCode: ''
    };
  },
  methods: {
    goHomeWithMessage() {
      this.$router.push('/');
    },
    submitCode() {
      if (this.filledCode.length !== 6) return;
      invite({
        code: this.filledCode,
        openid: this.$store.state.openid
      })
        .then((res) => {
          if (res.data.state) {
            this.$emit('showPromptBox', '');
            this.$store.commit('INVITE');
            this.goHomeWithMessage();
          } else {
            this.$emit('showPromptBox', '');
            this.goHomeWithMessage();
          }
        })
        .catch(() => {
          this.$emit('showPromptBox', '');
          this.goHomeWithMessage();
        });
    }
  }
};
</script>

<style lang="scss" scoped src="./style.scss"></style>



