<template>
  <transition name="fade">
    <div class="prompt-box">{{promptMessage}}</div>
  </transition>
</template>

<script>
export default {
  name: 'PromptBox',
  props: {
    promptMessage: {
      type: String,
      default: ''
    }
  }
};
</script>

<style lang="scss" scoped src="./style.scss"></style>



