<template>
  <ul class="scroll-message">
    <li
      v-for="message in messages"
      :key="message"
      :class="{ animation : animate }"
    >{{message}}</li>
  </ul>
</template>

<script>
export default {
  name: 'ScrollMessage',
  props: {
    messages: {
      type: Array,
      default() {
        return [
          '~',
          ''
        ];
      }
    },
    switchDelay: {
      default: 3000,
      type: Number
    }
  },
  data() {
    return {
      animate: false
    };
  },
  methods: {
    scroll() {
      this.animate = true;
      setTimeout(() => {
        this.animate = false;
        this.messages.push(this.messages.shift());
      }, 1200);
    }
  },
  watch: {
    messages() {
      // 
      if (this.messages.length <= 1) return;
      clearInterval(this.interval);
      // this
      this.interval = setInterval(() => {
        this.scroll();
      }, this.switchDelay);
    }
  },
  beforeDestroy() {
    clearInterval(this.interval);
  }
};
</script>

<style lang="scss" scoped src="./style.scss"></style>



