<template>
  <main class="main">
    <header class="header"></header>
    <svg
      class="animation"
      v-if="isAnswered && isRight"
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 130.2 130.2">
      <circle
        class="path circle"
        fill="none"
        stroke="#73AF55"
        stroke-width="6"
        stroke-miterlimit="10"
        cx="65.1" cy="65.1"
        r="62.1"
      >
      </circle>
      <polyline
        class="path check"
        fill="none"
        stroke="#73AF55"
        stroke-width="10"
        stroke-linecap="round"
        stroke-miterlimit="10"
        points="100.2,40.2 51.5,88.8 29.8,67.5 "
      >
      </polyline>
    </svg>
    <svg
      class="animation"
      v-else-if="isAnswered"
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 130.2 130.2">
      <circle
        class="path circle"
        fill="none"
        stroke="#D06079"
        stroke-width="6"
        stroke-miterlimit="10"
        cx="65.1"
        cy="65.1"
        r="62.1"
      >
      </circle>
      <line
        class="path line"
        fill="none"
        stroke="#D06079"
        stroke-width="10"
        stroke-linecap="round"
        stroke-miterlimit="10"
        x1="34.4"
        y1="37.9"
        x2="95.8"
        y2="92.3">
      </line>
      <line
        class="path line"
        fill="none"
        stroke="#D06079"
        stroke-width="10"
        stroke-linecap="round"
        stroke-miterlimit="10"
        x1="95.8"
        y1="38"
        x2="34.4"
        y2="92.2">
      </line>
    </svg>
    <CountdownTimer
      v-else
      class="clock"
      :rotateDeg="(totalTime - restTime) / totalTime * 360"
      :text="restTime"
    />
    <div class="background"></div>
    <transition name="slide">
      <div class="question" v-if="show">{{ question.questionTitle }}</div>
    </transition>
    <ul class="options">
      <Option
        v-if="show"
        v-for="(item, index) in question.questionOptions"
        :key="question.questionId + index"
        :optionsInfo="optionsInfo[index]"
        :rate="item.rate"
        @click.native.once="judgeResult(index)"
      >
        {{ item.content }}
      </Option>
    </ul>
    <StatusBar
      class="bottom-bar"
      :count="questionCount"
      :current="currentQuestion"
    />
    <ResultPage
      v-if="isEnd"
      :result="isRight"
      :title="showMessage.title"
      :text="showMessage.text"
      :score="totalScore"
    />
    <audio ref="timeout" src="../../assets/audio/timeout.wav"></audio>
    <audio ref="wrong" src="../../assets/audio/wrong.mp3"></audio>
  </main>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { END_GAME } from '../../store/mutation-types';
import CountdownTimer from '../../components/CountdownTimer';
import Option from '../../components/Option';
import ResultPage from '../../components/ResultPage';
import StatusBar from '../../components/StatusBar';
import { getQuestion, getScore } from '../../api';

export default {
  name: 'Quiz',
  data() {
    return {
      show: false,
      question: {
        questionTitle: '',
        questionOptions: [],
        questionId: ''
      },
      questionCount: 10, // 
      currentQuestion: 0, // 
      totalTime: 10, // 
      restTime: 10, // 
      isAnswered: false,
      isRight: false, // 
      isEnd: false, // 
      optionsInfo: [
        { selected: false },
        { selected: false },
        { selected: false },
        { selected: false }
      ],
      costTime: [], // 
      totalScore: 0, // 
      showMessage: {}, // 
      successMessage: [
        {
          title: 'YOU WIN',
          text: 'CHEERS, YOU GOT IT! '
        }, {
          title: '',
          text: ''
        }, {
          title: '',
          text: ''
        }, {
          title: '',
          text: 'buff'
        }
      ],
      failMessage: [
        {
          title: '',
          text: ''
        }, {
          title: '',
          text: ''
        }, {
          title: '',
          text: ''
        }, {
          title: '',
          text: ''
        }, {
          title: '',
          text: ''
        }
      ]
    };
  },
  methods: {
    // 
    resetData() {
      this.show = false;
      this.optionsInfo = [
        { selected: false },
        { selected: false },
        { selected: false },
        { selected: false }
      ];
      this.isRight = false;
      this.restTime = 10;
    },
    // 
    countdown() {
      clearInterval(this.countdownInterval);
      this.countdownInterval = setInterval(() => {
        if (this.restTime === 1) {
          this.playAudio('timeout');
        }
        if (this.restTime === 0) {
          this.optionsInfo = [
            { selected: true },
            { selected: true },
            { selected: true },
            { selected: true }
          ];
          clearInterval(this.countdownInterval);
          this.showResult();
          return;
        }
        this.restTime -= 1;
      }, 1000);
    },
    showQuestion() {
      // 
      this.resetData();
      // 
      getQuestion({ openid: this.openid })
        .then(({ data }) => {
          this.currentQuestion += 1;
          this.question = data;
          this.show = true;
          this.countdown();
          // 
          this.costTime[0] = new Date().getTime();
        });
    },
    // 
    judgeAnswer(index) {
      return index === 3;
    },
    // 
    judgeResult(index) {
      // 
      if (this.isAnswered) return;
      this.costTime[1] = new Date().getTime();
      if (this.judgeAnswer(index)) {
        // 
        this.isRight = true;
        this.$set(this.optionsInfo, index, {
          selected: true,
          state: true
        });
        this.computeScore();
        // 
        clearInterval(this.countdownInterval);
        // 
        if (this.currentQuestion === this.questionCount) {
          // 
          this.showResult();
        } else {
          // 
          setTimeout(() => {
            this.showQuestion();
          }, 1500);
        }
      } else {
        // 
        clearInterval(this.countdownInterval);
        this.$set(this.optionsInfo, index, {
          selected: true,
          state: false
        });
        this.playAudio('wrong');
        this.showResult();
      }
    },
    // 100
    computeScore() {
      getScore({ openid: this.openid, costTime: this.costTime })
        .then(({ data }) => {
          this.totalScore += data;
        });
      this.costTime = [];
    },
    showResult() {
      // 
      this.endGame();
      // 
      setTimeout(() => {
        // 
        this.getRandomMessage(this.isRight ? this.successMessage : this.failMessage);
        this.isEnd = true;
      }, 2000);
    },
    getRandomMessage(array) {
      this.showMessage = array[Math.floor(Math.random() * array.length)];
    },
    playAudio(type) {
      if (this.mute) return;
      switch (type) {
        case 'wrong':
          this.$refs.wrong.play();
          break;
        case 'timeout':
          this.$refs.timeout.play();
          break;
        default:
      }
    },
    ...mapMutations({
      endGame: END_GAME
    })
  },
  created() {
    // url
    if (!this.gameMode) {
      this.$router.push('/');
    }
  },
  mounted() {
    this.showQuestion();
  },
  beforeDestroy() {
    clearInterval(this.countdownInterval);
  },
  watch: {
    optionsInfo() {
      this.isAnswered = this.optionsInfo.some(element => element.selected);
    }
  },
  computed: mapState([
    'openid',
    'mute',
    'gameMode'
  ]),
  components: {
    CountdownTimer,
    Option,
    ResultPage,
    StatusBar
  }
};
</script>

<style lang="scss" scoped src="./style.scss"></style>



